import React from 'react';
import  Cascader  from 'antd/lib/cascader'
import axios from 'axios'
import 'antd/dist/antd.css'
class App extends React.Component{
  state={
    name:'',
    sex:'',
    date:'',
    city:'',
    street:'',
    tel:'',
    ipv4_1:'',
    ipv4_2:'',
    ipv4_3:'',
    ipv4_4:'',
    profile:''
  }
  inputChange(type,e){
    this.setState({[type]:e.target.value})
  }
  componentWillMount(){
    if(localStorage.form){
      this.setState(JSON.parse(localStorage.form))//web
    }
  }
  formSubmit(e){//
    e.preventDefault()
    let state=this.state
    let nowYear=new Date().getFullYear()
    let name=new RegExp("[\u4e00-\u9fa5]{2,4}")
    let tel=new RegExp('^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\\d{8}$')
    if(!name.test(state.name)){
      alert('2-4')
    }else if(!state.sex){
      alert('')
    }else if(!state.date){
      alert('')
    }else if(nowYear-state.date.slice(0,4)<18||nowYear-state.date.slice(0,4)>65){
      alert('18-65')
    }else if(!state.tel || !tel.test(state.tel)){
      alert('')
    }else{
      let formData=state
      axios.post('192.168.1.1',formData)
      .then(res=>{
        alert('')
        localStorage.removeItem("form")//web
        this.setState({//
          name:'',
          sex:'',
          date:'',
          city:'',
          street:'',
          tel:'',
          ipv4_1:'',
          ipv4_2:'',
          ipv4_3:'',
          ipv4_4:'',
          profile:''
        })
        e.target.reset()//
      })
      .catch(err=>alert(''))
    }
  }
  render(){
    localStorage.setItem("form",JSON.stringify(this.state)) //web  
    const options = [{
      value: 'zhejiang',
      label: '',
      children: [{
        value: 'hangzhou',
        label: '',
        children: [{
          value: 'xihu',
          label: '',
        }],
      }],
      }, {
      value: 'jiangsu',
      label: '',
      children: [{
        value: 'nanjing',
        label: '',
        children: [{
          value: 'zhonghuamen',
          label: '',
        }],
      }],
    }];        // 
    return (
      <div>
        <form onSubmit={this.formSubmit.bind(this)}>
          <label>*<input onChange={this.inputChange.bind(this,'name')} value={this.state.name} placeholder='2-4'/></label><br/>
          <label>*
            <input type="radio" onClick={this.inputChange.bind(this,'sex')} name="sex" value="man" />
            <input type="radio" onClick={this.inputChange.bind(this,'sex')} name="sex" value="woman"/>
          </label><br/>
          <label>*<input type='date' onChange={this.inputChange.bind(this,'date')} value={this.state.date}/></label><br/>
          <label>*<input onChange={this.inputChange.bind(this,'tel')} value={this.state.tel}/></label><br/>
          
            :<Cascader
              options={options}
              style={{width:'200px'}}
              onChange={value=>this.setState({city:value})}
              placeholder=""
              value={this.state.city}
            />
          <label>:<input onChange={this.inputChange.bind(this,'street')} value={this.state.street}/>
          </label><br/>
          <label>IP
            <input onChange={this.inputChange.bind(this,'ipv4_1')} value={this.state.ipv4_1} style={{width:'25px'}}/>.
            <input onChange={this.inputChange.bind(this,'ipv4_2')} value={this.state.ipv4_2} style={{width:'25px'}}/>.
            <input onChange={this.inputChange.bind(this,'ipv4_3')} value={this.state.ipv4_3} style={{width:'25px'}}/>.
            <input onChange={this.inputChange.bind(this,'ipv4_4')} value={this.state.ipv4_4} style={{width:'25px'}}/>
          </label><br/>
          <label>
            <br/>
            <textarea onChange={this.inputChange.bind(this,'profile')} placeholder="140" value={this.state.profile} style={{width:'300px',height:'100px'}}/>
          </label><br/>
          <input type='submit'/>
        </form>
      </div>
    )
  }
}
export default App



